<template>
    <div>
        <div class="field-part">
            <mt-cell title=":"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title=""
                     :value="productsList.code"
                     is-link></mt-cell>
            <mt-cell title=""
                     :value="productsList.name"
                     is-link></mt-cell>
            <mt-cell title=""
                     :value="productsList.public_person"
                     is-link></mt-cell>
            <mt-cell title=""
                     :value="productsList.status"
                     is-link></mt-cell>
            <mt-cell title=""
                     :value="productsList.s_date"
                     is-link></mt-cell>
            <mt-cell title=""
                     :value="productsList.b_date"
                     is-link></mt-cell>
            <mt-cell title=""
                     :value="productsList.e_date"
                     is-link></mt-cell>
            <mt-cell title=""
                     :value="productsList.yield"
                     is-link></mt-cell>
        </div>
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       @click="$back"></mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="doNext"></mt-button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            productsList: {
                name: '',
                code: 'fly001',
                public_person: '',
                s_date: '20160103',
                b_date: '20160201',
                e_date: '20160209',
                status: '',
                yield: '20.00000%'
            }
        }
    },
    methods: {

        doNext(item) {
            this.$router.push({ name: 'financialProductsTransferSubscriptionDo', params: { info: item } });
        }
    },
    created() {
        
        console.log(this.$route.params.info);
        //this.productsList = Object.assign({}, this.$route.params.info);
        this.$store.dispatch('showLoading', false);
    }
}
</script>


