<template>
    <div>
        <account-query-info :data-copy="dataList" :map="listMap" :filter="filter" ></account-query-info>
    
        <div class="field-part">
            <mt-cell title="" v-model="infoData.type" @click.native="showTypeSheet = true"></mt-cell>
            <mt-field label=":" v-model="infoData.ammount" type="text"></mt-field>
            <mt-field label=":" v-model="infoData.idcard_No" type="text"></mt-field>
            <mt-field label=":" v-model="infoData.password" type="password"></mt-field>
        </div>
    
        <div class="field-action">
            <mt-button type="default" plain size="small" @click="$back"></mt-button>
            <mt-button type="primary" size="small" @click="doConfirm()"></mt-button>
        </div>
        <!--        -->
        <mt-actionsheet :actions="typeSheetList" v-model="showTypeSheet"></mt-actionsheet>
    </div>
</template>

<script>
import { Toast } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../../services/current-regular';
import AccountQueryInfo from '../account-query-info'
export default {
    components: {
        AccountQueryInfo
    },
    data() {
        return {
            dataList: {},
            listMap: {
                current: ':',
                regular: ':',
                balance: ':',
                currency: ':',
                status: ':',
                term: ':',
                start_date: ':',
                end_date: ':',
                rate: ':'
            },
            filter: ['id', 'type', 'state'],
            infoData: {
                type: '',
                ammount: '',
                idcard_No: '',
                password: ''
            },
            showTypeSheet: false,
            typeSheetListPart: [{
                name: '',
                method: this.typeCallback
            }],
            typeSheetListAll: [{
                name: '',
                method: this.typeCallback
            }, {
                name: '',
                method: this.typeCallback
            }],
            typeSheetList: [],
            id: ''
        }
    },
    methods: {
        doConfirm() {
            if (this.$validate.isEmpty(this.infoData.ammount)) {
                this.$msg('', 'error', 1000);
                return false;
            }

            if (!this.$validate.isAmount(this.infoData.ammount)) {
                this.$msg('', 'error', 1000);
                return false
            }
            if (this.$validate.isEmpty(this.infoData.idcard_No)) {
                this.$msg('', 'error', 1000);
                return false;
            }
            if (!this.$validate.isIDcard(this.infoData.idcard_No)) {
                this.$msg('', 'error', 1000);
                return false;
            }
            if (this.$validate.isEmpty(this.infoData.password)) {
                this.$msg('', 'error', 1000);
                return false;
            }
            if (!this.$validate.isPwd(this.infoData.password)) {
                this.$msg('', 'error', 1000);
                return false;
            }
            this.$router.push({ name: 'currentRegularMsg', params: { go: -3 } })
        },
        typeCallback(item) {
            this.infoData.type = item.name;
        }

    },
    created() {
        serviceAccount.getCurrentList()
            .then((res) => {
                let self = this;
                if (!res.data.status) {
                    Toast(res.data.message);
                    return;
                }
                let _data = res.data.list.filter((item) => {
                    return item.id === self.$route.query.id;
                })
                this.dataList = _data[0];
                self.id = this.dataList.type === "" ? "all" : "part";
                this.typeSheetList = this.id === 'all' ? this.typeSheetListAll : this.typeSheetListPart;
                this.infoData.type = this.typeSheetList[0].name;
            }, (error) => {
                Toast(err);
            })
    }
}

</script>



