<template>
    <div>
        <div class="field-part">
            <mt-cell title=":"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell v-for="(index,value) in item"
                     :title="value"
                     :key="index"
                     is-link></mt-cell>
        </div>
        <div class="reminder">
            <p class="reminder-title"></p>
            <p></p>
            <p>1.</p>
            <p>2.</p>
            <p>3.</p>
            <p></p>
        </div>
        <div>
            <p class="btn-title"></p>
            <div class="field-action">
                <mt-button type="primary"
                           size="small"
                           @click="$back"></mt-button>
                <mt-button type="primary"
                           size="small"
                           @click="doAgree"></mt-button>
            </div>
        </div>
        <div class="reminder">
            <p class="reminder-title"></p>
            <p class="reminder-content">  
            </p>
        </div>
    </div>
</template>

<script>
import { MessageBox  } from 'mint-ui-rem-dpr';
export default {
    data() {
        return {
            item: ['', '', '', '']
        }
    },
    methods: {
        doAgree() {
            MessageBox.confirm('?').then( (action) => {
                this.$router.push({name: 'financialProductsTransferSubscriptionDetail', params: { info: this.$route.params.info } })
            }, (action) => {
                
            });
        }
    },
    created() {
        //console.log(this.$route.params.info);
        this.$store.dispatch('showLoading', false);
    }
}
</script>
<style lang="scss" scoped>
@import '../../../assets/styles/mixins';

.btn-title {
    text-align: center;
    margin: 20px auto;
}
</style>


