<template>
    <div>
        <div class="field-part">
            <mt-cell title=":"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title=":"
                     :value="dataForm.grade"
                     type="text"></mt-cell>
            <mt-cell title=":"
                     :value="dataForm.date"
                     type="text"></mt-cell>
            <mt-cell title=":"
                     :value="dataForm.scroll"
                     type="text"></mt-cell>
        </div>
