        </div>
        <div class="reminder">
            <p class="reminder-title"></p>
            <p class="reminder-content">  
            </p>
        </div>
    </div>
</template>

<script>
import { Toast, Actionsheet } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../../services/account';
export default {
    data() {
        return {
            dataForm: {
                grade: '',
                date: '20200908',
                scroll: '40'
            }
        }
    },
    methods: {
        doReassessment() {
            this.$router.push({ name: 'financialProductsReassessment' })
        }
    },
    created() {
        this.$store.dispatch('showLoading', false);
    }
}

</script>


