<template>
    <div>
        <div class="reminder">
            <p class="reminder-title"></p>
            <p></p>
            <p>1.</p>
            <p>2.</p>
            <p>3.</p>
            <p></p>
        </div>
        <div class="field-part">
            <mt-cell title=":"></mt-cell>
        </div>
        <div class="field-part"
             v-for="item in radioData">
            <mt-radio :title="item.title"
                      v-model="item.value"
                      :options="item.options">
            </mt-radio>
        </div>
        <div class="field-part">
            <mt-field label=""
                      placeholder=""
                      type="password"
                      v-model="password"></mt-field>
        </div>
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       @click="$back"></mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="doSubmit()"></mt-button>
    
        </div>
        <div class="reminder">
            <p></p>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            password: '',
            radioData: [{
                title: '1',
                value: '',
                options: ['60', '4560', '3045', '30']
            }, {
                title: '2',
                value: '',
                options: ['', '', '', '']
            }, {
                title: '3',
                value: '',
                options: ['', '', '', '']
            }]
        }
    },
    methods: {
        doSubmit() {
            let _value = [];
            let flag = 0;
            this.radioData.forEach((item) => {
                _value.push(item.value);
            });
            _value.forEach((item) => {
                if (this.$validate.isEmpty(item)) {
                    flag++;
                }
            })
            if (flag > 0) {
                this.$msg('', 'error', 1000);
                return false;
            }
            if (this.$validate.isEmpty(this.password)) {
                this.$msg('', 'error', 1000);
                return false;
            }
            if (!this.$validate.isPwd(this.password)) {
                this.$msg('', 'error', 1000);
                return false;
            }
            this.$router.push({name: 'financialProductsReassessmentResult'})
        }
    }
}

</script>




